// var get = require('lodash.get');
import get from 'lodash.get'
console.log(get)
import {
	mapState,
	mapMutations
} from 'vuex'
export default {
	data(){
		return {
			dataSource:{}
		}
	},
	computed: {
		...mapState(['sourceId']),
		name() {
		      return get(this.dataSource, 'basic.name')
		    },
		    basic() {
		      return get(this.dataSource, 'basic') || { id: this.sourceId }
		    },
		    career() {
		      return (
		        get(this.dataSource, 'career') || {
		          driverInfo: { id: this.sourceId }
		        }
		      )
		    },
		    license() {
		      return (
		        get(this.dataSource, 'license') || {
		          driverInfo: { id: this.sourceId }
		        }
		      )
		    },
		    currentRecord() {
		      return (
		        get(this.dataSource, 'currentRecord') || {
		          driverInfo: { id: this.sourceId }
		        }
		      )
		    },
		    certificate() {
		      return (
		        get(this.dataSource, 'certificate') || {
		          driverInfo: { id: this.sourceId }
		        }
		      )
		    }
	},
	mounted(){
		this.fetch()
	},
	methods:{
		async fetch(){
			const {sourceId,$http} = this;
			this.dataSource = await $http.get(`api/info/driver/${sourceId}`)
			console.log(this.dataSource)
		}
	}
}
